.rates {
  position: relative;
  background-color: $lightgrey;
  min-height: 480px;
  overflow: hidden;

  &::before {
    content: "";
    height: 32px;
    width: 100%;
    position: absolute;
    top: -1px;
    left: 0;
    background-color: #ffffff;
    clip-path: polygon(100% 0, 0 0, 0 100%);
  }
}

.rates__wrapper {
  position: relative;
  min-height: 279px + 127px;
}

.rates__table-wrapper {
  position: absolute;
  padding-top: 127px;
  left: -256px;
  width: 276px * 3 + 4px;
}

@media (min-width: $tablet-width) {
  .rates {
    min-height: 2515px - 2130px;
    padding-left: 20px;
    padding-right: 20px;
    background-image: none;
    background-color: $lightgrey;

    &::before {
      height: 2172px - 2132px;
    }
  }

  .rates__wrapper {
    padding-top: 101px;
    padding-bottom: 68px;
    min-height: 2450px - 2230px;
  }

  .rates__table-wrapper {
    position: static;
    padding: 0;
    width: 100%;
  }

  .rates__toggles {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .rates {
    min-height: 3155px - 2620px;
    padding-left: 130px;
    padding-right: 130px;

    &::before {
      height: 2721px - 2620px;
    }
  }

  .rates__wrapper {
    padding-top: 159px;
    padding-bottom: 99px;
    min-height: 2450px - 2230px;
  }

  .rates__table-wrapper {
    position: static;
    padding: 0;
    width: 100%;
  }

  .rates__toggles {
    display: none;
  }
}
