.main-nav {
  position: relative;
  width: auto;
  box-sizing: border-box;
}

.main-nav__logo-link {
  display: inline-block;
  min-height: 40px;
  padding: 27px 20px 42px 20px;
}

@media (max-width: 1199px) {
  .main-nav--opened {
    background-color: #283645;
  }
  .main-nav--closed {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .main-nav__logo-link .main-nav__logo:hover {
    fill: #d22856;
  }
  .main-nav__logo-link .main-nav__logo:active {
    fill: #ffffff;
    opacity: 0.3;
  }

  .main-nav__toggle {
    position: absolute;
    display: block;
    box-sizing: border-box;
    top: 0;
    right: 0;
    z-index: 2;
    min-height: 100%;
    padding: 0;
    cursor: pointer;
    border: none;
    background-color: transparent;
  }

  .main-nav--opened .main-nav__toggle {
    width: 63px;
  }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 31px;
    right: 17px;
    width: 30px;
    height: 3px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
    background-color: #d22856;
  }
  .main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
    opacity: 0.3;
  }

  .main-nav--closed .main-nav__toggle {
    width: 90px;
  }
  .main-nav--closed .main-nav__toggle::before, .main-nav--closed .main-nav__toggle::after {
    display: none;
  }

  .main-nav--opened .main-nav__burger-menu {
    display: none;
  }

  .main-nav--closed .main-nav__burger-menu {
    display: inline-block;
  }
  .main-nav--closed .main-nav__burger-menu:hover {
    fill: #d22856;
  }
  .main-nav--closed .main-nav__burger-menu:active {
    opacity: 0.3;
  }

  .main-nav--opened .main-nav__list-wrapper {
    position: absolute;
    left: 0;
    width: 100%;
  }

  .main-nav--closed .nav-list__item {
    display: none;
  }

  .main-nav--opened .nav-list__item {
    position: relative;
  }
  .main-nav--opened .nav-list__item:first-of-type {
    border-top: 1px solid #000000;
  }

  .main-nav--opened .nav-list a {
    position: relative;
    display: block;
    padding-top: 14px;
    padding-bottom: 15px;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
    text-align: center;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #283645;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid #000000;
  }
  .main-nav--opened .nav-list a:hover {
    color: #d22856;
  }
  .main-nav--opened .nav-list a:active {
    color: rgba(210, 40, 86, 0.3);
  }

  .main-nav--nojs {
    background-color: #283645;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list-wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .nav-list__item {
    display: block;
  }

  .main-nav--nojs .nav-list__item--current a {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (max-width: 659px) {
  .main-nav--opened {
    min-height: 70px;
  }
  .main-nav--closed {
    min-height: 66px;
  }

  .main-nav__logo-link {
    min-height: 22px;
    padding: 22px 20px 25px 20px;
  }

  .main-nav--closed .main-nav__logo-link {
